.btn {
  color: #ffffff;
  background-color: #363230;
  border-color: #121111;
}

.btn:hover,
.btn:focus,
.btn:active,
.btn.active,
.open .dropdown-toggle.btn {
  color: #ffffff;
  background-color: #9A8478;
  border-color: #121111;
}

.btn:active,
.btn.active,
.open .dropdown-toggle.btn {
  background-image: none;
}

.btn.disabled,
.btn[disabled],
fieldset[disabled] .btn,
.btn.disabled:hover,
.btn[disabled]:hover,
fieldset[disabled] .btn:hover,
.btn.disabled:focus,
.btn[disabled]:focus,
fieldset[disabled] .btn:focus,
.btn.disabled:active,
.btn[disabled]:active,
fieldset[disabled] .btn:active,
.btn.disabled.active,
.btn[disabled].active,
fieldset[disabled] .btn.active {
  background-color: #363230;
  border-color: #121111;
}

.btn .badge {
  color: #363230;
  background-color: #ffffff;
}

